.slider {
    -webkit-appearance: none;
    position: absolute;
    width: 100%;
    height: 6px;
    left: -3px;
    border-radius: 5px;  
    background: rgba(0, 0, 0, 0.493);
    outline: none;
    opacity: 0.8;
    transition: opacity 0.5s ease-in-out;
    -webkit-transition: opacity 0.5s ease-in-out;
}
.slider-thumb{
    pointer-events: none;
    position: relative;
    width: 25px; /* Set a specific slider handle width */
    height: 25px; /* Slider handle height */
    border-radius: 25px;
    background: #CBFA01; /* Yellow background */
    z-index: 10;
    opacity: 1;
    /* outline: solid;
    outline-color: rgba(255, 255, 255, 1);
    outline-width: 5px; */
    transform: translate(-50%,0%);
    bottom: 8px;
    /* left : 50%; */
    left : calc(50% + 100px);
}

.slider-thumb::before{
    content: '';
    display: block;
    pointer-events: none;
    position: relative;
    width: 25px;
    height: 25px;
    border-radius: 25px;
    background: rgba(255, 0, 0, 0);
    z-index: 10;
    opacity: 1;
    outline: solid;
    outline-width: 5px;
    outline-color: white;
    z-index: 10;
    transition: 0.3s;
}

.slider-filler{
  pointer-events: none;
  position: absolute;
  top : 1px;
  width: 50%;
  height: 7px;
  border-radius: 6px;  
  background: #CBFA01;    /* Old color = #cbfa01 */
  outline: none;
  opacity: 0.9;
  -webkit-transition: .2s;
  transition: opacity .2s;
  pointer-events: none;
  z-index: 3;
}
