  z-index: 100;
  outline: solid;
  outline-color: rgba(255, 255, 255, 0);
  outline-width: 5px;
  transition: 0.3s;
}
  
input::-moz-range-thumb {
  width: 25px; /* Set a specific slider handle width */
  height: 25px; /* Slider handle height */
  position: relative;
  background: #CBFA01; /* Green background */
  opacity: 0;
  border-radius: 5px;
  cursor: pointer; /* Cursor on hover */
  z-index: 100;
  outline: solid;
  outline-color: white;
  outline-width: 5px;
  transition : 0.3s;
}

