/* #region Likert 5 */
#likert5-wrapper {
    position: absolute;
    width: 100%;
    top: 25vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 2;
    animation: likert5FadeIn 0.5s ease-in 0s 1 normal forwards;
  }

  @keyframes likert5FadeIn {
      0% { opacity: 0; }
      100% { opacity: 1; }
  }

  @keyframes likert5FadeOut {
    0% { opacity: 1; }
    100% { opacity: 0; }
  }
  
  form {
    width: 90%;
    max-width: 500px;
  }
  form #likert5-slider {
    display: flex;
    flex-direction: row;
    align-content: stretch;
    position: relative;
    width: 100%;
    height: 50px;
    top: 50vh;
    /* bottom: 50vh; */
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  /* Center Line Style */
  form #likert5-slider::before {
    content: " ";
    position: absolute;
    height: 5px;
    width: 100%;
    width: calc(100% * (4 / 5));
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: rgba(49, 49, 49, 0.363);
  }
  form #likert5-slider input, form #likert5-slider label {
    box-sizing: border-box;
    flex: 1;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
  }
  form #likert5-slider label {
    display: inline-block;
    position: relative;
    width: 20%;
    height: 100%;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  /* Label Text default style */
  form #likert5-slider label::before {
    content: attr(likert-scale-value);
    position: absolute;
    width: auto;
    height: 80px;
    display: flex;  /* */
    align-items: flex-end;  /* */
    overflow: hidden;
    left: 50%;
    padding-top: 10px;
    transform: translate(-50%, -85px);  /* -45px */
    font-size: 14px;
    text-align: center;
    color: white;
    letter-spacing: 0.4px;
    font-weight: 400;
    opacity: 0.85;
    transition: all 0.15s ease-in-out;
  }
  /* Radio button style default */
  form #likert5-slider label::after {
    content: " ";
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 20px;
    height: 20px;
    border: 2px solid rgb(255, 255, 255);
    background: rgba(125, 39, 252, 1);
    border-radius: 50%;
    pointer-events: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    z-index: 1;
    cursor: pointer;
    transition: all 0.15s ease-in-out;
  }
/* #region Setting different circle size for each child */
  form #likert5-slider #likert5_negativeTwo::after{
    width: 25px;
    height: 25px;
    border-radius: 50%;
 }

 form #likert5-slider #likert5_positiveTwo::after{
    width: 25px;
    height: 25px;
    border-radius: 50%;
 }

 form #likert5-slider #likert5_negativeOne::after{
    width: 18px;
    height: 18px;
    border-radius: 50%;
 }

 form #likert5-slider #likert5_positiveOne::after{
    width: 18px;
    height: 18px;
    border-radius: 50%;
 }

 form #likert5-slider #likert5_neutral::after{
    width: 15px;
    height: 15px;
    border-radius: 50%;
 }
/* #endregion */

  /* Setting radio button size while hovering */
  /* form #likert5-slider label:hover::after {
    transform: translate(-50%, -50%) scale(1.25);
  } */
  form #likert5-slider input {
    display: none;
  }
  form #likert5-slider input:checked + label::before {
    font-weight: 700;
    opacity: 1;
  }
  /* Radio button style while checked */
  form #likert5-slider input:checked + label::after {
    border-width: 3px;
    transform: translate(-50%, -50%) scale(1);
