    color: white;
    font-weight: 300;
    font-family: 'Rubik',sans-serif;
    width: 75%;
    font-size: calc(0.7em + 0.5vw);
    text-shadow:  black 0 0 50px;
    margin-bottom: 1.8vh;
    text-align: center;
    opacity: 0;
    animation: result-subtext-fadeIn 0.3s ease-out 1s 1 normal forwards;
}

@keyframes result-subtext-fadeIn{
    0% { opacity : 0}
    100% { opacity : 1}
}

.results-more-button{
    background: #cbfa01;
    font-weight: 600;
    font-family: 'Rubik', sans-serif;
    color: rgba(0, 0, 0, 1);
    padding: 3px 15px;
    border:none;
    cursor: pointer;

    font-size: calc(1em + 1vw);
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);
    margin-bottom: 1.8vh;
}

.results-footer-social-container{
    width: min(60%,250px);
    border: 3px solid;
    border-color: white;
    animation: pulse 3s linear infinite ;
}

@keyframes pulse {
    from{
        border-color: white;
    }
    50%{
        border-color: rgba(255, 255, 255, 0.479);
    }
    to{
        border-color: white;

    }

}

.results-footer-text{
    font-family: 'Rubik',sans-serif;
    font-weight: 300;
    color: white;
    text-align: center;
    font-size: calc(0.7em + 0.5vw);
    margin: 0 auto;
    padding-top: 8px;
}

.results-footer-social{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 10px auto 8px auto;
    
}

.results-footer-social-icon{
    width: 18px;
    margin: 0 0 0 5px;
}

.results-footer-social-icon.facebook{
    content: url('../static/undp_fb_logo.png');

}

.results-footer-social-icon.twitter{
    content: url('../static/undp_twitter_logo.png');
}



.links-page-container{
    background:  #1C1B1B;
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    z-index: 6;
    overflow: auto ;
}

#links-menu-holder{
    background: #1C1B1B;
}

.links-main-container{
    width: min(79.9%, 1000px);
    display: flex;
    flex-direction: row;
    margin: 0 auto;
    margin-top: calc(5px + 5%);
    margin-bottom: 150px;
}


.links-body-container{
    display: flex;
    flex-direction: column;

    width: min(100%, 750px);
    

    /* margin: 0 0 0 auto; */
    /* margin-top: calc(5px + 5%); */
 
}

.links-result-header-container{
    font-family: 'Rubik',sans-serif;
    font-size: calc(2em + 2vw);
    color: white;
    margin: 0 0 15px 0;
}

.links-result-subtext-container{
    font-family: 'Rubik',sans-serif;
    font-size: calc(0.5em + 0.5vw);
    color: white;
    font-weight: 300;
    text-align: justify;
    /* text-justify: inter-word; */
    line-height: 1.6;
    margin-bottom: 2vh;
}


.additional-resources-grid-header{
    color: white;
    font-family: 'Rubik',sans-serif;
    font-size: calc(0.8em + 0.8vw);
    margin-bottom: 15px;
}

.resource-link-container-top{
    width: 100%;
    background: rgba(36,35,35,1);
    color: white;
    padding: 10px;
    margin-bottom: 8px;
    box-sizing: border-box;

    font-family: 'Rubik',sans-serif;
    font-size: calc(0.6em + 1vw);
}

.resource-link-container-bottom{
    width: 100%;
    font-family: 'Rubik',sans-serif;
    font-size: calc(0.6em + 1vw);

    display: flex;
    flex-direction: row;

    margin-bottom: 15px;
}

.resource-link-container-left{
    width: calc(60% - 4px);
    margin-right: 8px;
    padding: 10px;
    background: rgba(36,35,35,1);
    color: white;
    box-sizing: border-box;
}

.resource-link-container-right{
    width: calc(40% - 4px);
    background: rgba(36,35,35,1);
    box-sizing: border-box;
    padding: 10px;
}

.resource-link:any-link{
    color: white;
    text-decoration: none;
}

.resource-link-container-top > .resource-link:any-link{
    color: white;
}

.additional-resources-list-container{
    /* margin-bottom: 150px; */

}

.additional-resources-list-header{
    font-family: 'Rubik',sans-serif;
    font-size: calc(0.8em + 1vw);
    padding-bottom: 20px ;
    border-bottom: 0.5px solid rgba(255, 255, 255, 0.397);
    color: white;
}

.additional-resources-list-item{
    display: flex;
    flex-direction: row;
    border-bottom: 0.3px solid rgba(255, 255, 255, 0.219);
 
}

.additional-resources-list-item-text{
    font-family: 'Rubik',sans-serif;
    font-size: calc(0.7em + 0.5vw);
    padding-bottom: 15px ;
    color: white;
    width: 80%;
    padding-top: 25px;
    background: #C4C4C41A;

}

.additional-resources-list-item-text > .resource-link{
    transition: color 0.2s;
}

.resource-link-block{
    margin: auto;
}

.resource-link-icon{
    /* content:url('../static/link_button.png'); */
    background-image: url('../static/link_button.png');
    background-repeat: no-repeat;
    background-size: 16px;
    background-position: center;
    width:16px;
    height:16px;
    margin: auto;
    position: relative;
}

.links-email-submission-container{
    margin-top: 10vw;
    margin-left: 20px;
    border: 2px solid rgba(255, 255, 255, 0.438);
    padding: 30px;
    box-sizing: border-box;
    height: fit-content;

    display: flex;
    flex-direction: column;

    width: min( calc(100% - 20px), 250px);
}

.links-email-submission-text{
    width: 100%;
    box-sizing: border-box;
    color: white;
}

.links-email-submission-header{
    font-family: 'Rubik',sans-serif;
    font-size: calc(0.8em + 0.8vw);
    margin-bottom: 10px ;
    font-weight: 300;
}

.links-email-submission-subtext{
    font-family: 'Rubik',sans-serif;
    font-size: calc(0.4em + 0.4vw);
    font-weight: 300;
    margin-top:calc(10px + 1vw) ;
    margin-bottom: calc(25px + 1vw);
}

#links-email-input{
    background: #1C1B1B;
    border-radius: 0;
    border: 1px solid rgb(141, 141, 141);
    color: white;
    margin: 20px 0 ;
    /* width: calc(100% - 20px); */
    width: 100%;
    
    height: 1.2em;
    font-size: 1em;
    outline: none;
    box-sizing: border-box;

    padding: 15px 10px;
}

#links-email-input:focus{
    border: 1px solid white;
    background: #1C1B1B;
    border-radius: 0;
    color: white;
}

#links-email-submit-button{
    background: #cbfa01;
    font-weight: 600;
    font-family: 'Rubik', sans-serif;
    font-size: 1em;
    color: rgba(0, 0, 0, 1);
    padding: 5px 15px;
    border:none;
    cursor: pointer;
    
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.5);

    /* margin: 0 0 20px 0; */
}

.links-email-submit-indicator{
    color: #cbfa01;
    font-size: 1em;
    width: fit-content;
    transition: width 0.25s;
    display: inline;
    padding-left: 10px;
}

.links-email-submit-indicator:empty{
    width:0px;
}


.links-footer{
    font-weight: 500;
    align-items: center;
    text-align: center;
    width: 80%;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center;

    position: fixed;
    bottom: 0;
    left: 50%;

    transform: translateX(-50%);

    background: #1C1B1B;
    margin: 0 auto;
}



.links-footer-divider{
    width: 100%;
    height: 0.5px;
    background: rgba(255, 255, 255, 0.2);
    margin: 10px 0;
}

.links-footer-links{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    margin: 5px 0 25px 0;
}

.links-footer-privacy-policy{
    font-size: 0.6em;
    font-weight: 300;
    color: white;
    font-family: 'Roboto',sans-serif;
    letter-spacing: 0.5px;
}

.links-footer-social{
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.links-footer-social-icon{
    width: 18px;
    margin: 0 0 0 5px;
}

.links-footer-social-icon.facebook{
    content: url('../static/undp_fb_logo.png');
}

.links-footer-social-icon.twitter{
    content: url('../static/undp_twitter_logo.png');
}

.links-footer-social-icon.youtube{
    content: url('../static/undp_yt_logo.png');
}

#control-ok-button{
    position: absolute;
    right: 5vw;
    width: calc(12vh + 3px);
    border-radius: 6px;
    border: none;
    box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.32);
}

.answer-container{
    width: 60%;
    height: fit-content;

    /* font-weight: 700; */
    position: absolute;
    bottom: 14vh;
    left: 20%;
    text-align: center;
    
    color: white;
    text-shadow: 0px 6px 6px rgba(0, 0, 0, 0.25);
    opacity: 0%;

    font-size: calc(0.9em + 1.3vw);
}

@keyframes fadein {

    0%{
        opacity: 0%;
    }
    100%{
        opacity: 100%;
    }
}

@keyframes fadeout {

    0%{
        opacity: 100%;
    }
    100%{
        opacity: 0%;
    }
}


.hidden-radio{
    display: none;
}

.about-answers-container{
    width: 100vw;
    height: 35vh;
    position: absolute;
    display: flex;
    flex-direction: row ;
    align-items: center;
    justify-content: space-around;
    left: 0;
    bottom: 25vh ;
}



.answer-box{
    width: 100%;
    height: max-content;
    padding: 10px;
    margin: 2vh 0;
    background: white;
    font-weight: 800;
    border-radius: 5px;
    color: rgba(0, 0, 0, 0.575);
    user-select: none;
    cursor: pointer;
    box-shadow: 0 0 10px 2px rgba(0, 0, 0, 0.32) ;
}

.answer-box.selected{
    background: #ade6ff;
}

@media (hover: hover) {
    form #likert5-slider label:hover::after {
        transform: translate(-50%, -50%) scale(1.25);
    }

    .answer-box:hover{
        background: #cdf0ff;
    }

    .country-skip-button:hover{
        transform: translate(-50%,-4px);
        box-shadow: 3px 3px 16px rgba(0, 0, 0, 0.20)
                    /* , 0 5px 10px 0 rgba(0, 0, 0, 0.5) inset  */
                    ;
        /* text-shadow: 0 5px 10px rgba(0, 0, 0, 0.5); */
        background-color: white;
        color: #6f23fc;

    }
    .additional-resources-list-item-text > .resource-link:hover{
        
        color: #cbfa01;
    }
}

.answer-box:active{
    background: #5d99b3;
}

.country-skip-button:active{
    transform: translate(-50%,0);
    background-color: white;
    color: #6f23fc;
    mix-blend-mode: screen;
    transition: none;
    text-shadow: none;
    box-shadow: 3px 3px 16px rgba(0, 0, 0, 0.20);
}



.control-button:disabled{
    opacity: 50%;
    cursor: not-allowed;
}

.survey-start-button:disabled{
    opacity: 50%;
    cursor: not-allowed;
}



